import { BrowserRouter, Route, Routes, Link } from "react-router-dom";
import { useNavigate } from "react-router-dom";

import { useState, useEffect } from "react";
const Tutorial = (tt) => {
  const [tuto, setTuto] = useState(true);

  useEffect(() => {
    const localsto = localStorage.getItem("checked");
    if (localsto) {
      setTuto(false);
    }
  }, []);

  const ischecked = () => {
    localStorage.setItem("checked", true);
    setTuto(false);
  };
  return (
    <>
      {tuto && (
        <div className="tutorial-con first">
          <div className="tuto-wrap">
            <h1 className="first"> .</h1>
            <div className="box">
              <span className="white"> </span>  
                  .{" "}
              <Link to="/lettertuto"></Link>
            </div>
            {/*  <button onClick={ischecked}>.</button> */}
          </div>
        </div>
      )}
    </>
  );
};

export default Tutorial;
